span.loading {
  display: inline-block;
  width: 100px;
  height: 100px;
  border-radius: 50%;
  border: 5px solid #ccc;
  border-top: 5px solid rgb(255, 157, 0);
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -webkit-animation: 1.2s cubic-bezier(0.31, 0.85, 0.69, 0.85) infinite revolve;
          animation: 1.2s cubic-bezier(0.31, 0.85, 0.69, 0.85) infinite revolve;
}
.loadingWrapper {
    width: 100%;
    padding: 20px 10px;
    text-align: center;
}
@-webkit-keyframes revolve {
  100% {
    -webkit-transform: rotateZ(360deg);
            transform: rotateZ(360deg);
  }
}
@keyframes revolve {
  100% {
    -webkit-transform: rotateZ(360deg);
            transform: rotateZ(360deg);
  }
}



